// pages/signup.jsx
import { useState } from "react";
import Lottie from "lottie-react";
import signupAnimation from "../animations/signup.json";
import "../styles/signup.css";

export default function Signup() {
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  return (
    <div className="signup-page">
      <div className="signup-container">

        {/* Form Section */}
        <div className="signup-form-section">
          <h2>Create Account</h2>
          <p>Join Threadix and start shopping today!</p>

          <form className="signup-form">
            <div className="form-group">
              <label>Full Name</label>
              <input type="text" placeholder="Enter your full name" value={fullName} onChange={(e)=>setFullName(e.target.value)} required />
            </div>

            <div className="form-group">
              <label>Email</label>
              <input type="email" placeholder="Enter your email" value={email} onChange={(e)=>setEmail(e.target.value)} required />
            </div>

            <div className="form-group">
              <label>Password</label>
              <input type="password" placeholder="Enter your password" value={password} onChange={(e)=>setPassword(e.target.value)} required />
            </div>

            <div className="form-group">
              <label>Confirm Password</label>
              <input type="password" placeholder="Confirm your password" value={confirmPassword} onChange={(e)=>setConfirmPassword(e.target.value)} required />
            </div>

            <button type="submit" className="signup-btn">Sign Up</button>
          </form>

          <p className="signup-bottom-text">
            Already have an account? <a href="/login">Login</a>
          </p>
        </div>

        {/* Animation Section */}
        <div className="signup-animation-section">
          <Lottie animationData={signupAnimation} loop={true} />
        </div>

      </div>
    </div>
  );
}
